.contact {
  grid-column-start: 1;
  grid-column-end: 3;
  display: grid;
  grid-template-columns: 63.33% 33.33%;
  column-gap: 3.33%;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    column-gap: 0;
  }

  @include vp-767 {
    padding-bottom: 40px;
  }
}

.contact__title {
  font-size: 28px;
  line-height: 30px;
  font-weight: 400;
  margin: 0 0 25px;

  @include vp-767 {
    font-size: 24px;
    line-height: 30px;
    margin: 0 0 16px;
  }
}

.contact__user {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 45px;

  @include vp-767 {
    gap: 10px;
    padding-bottom: 16px;
  }
}

.contact__form {
  padding-top: 45px;
}

.contact__user fieldset {
  padding: 0 0 0 20px;
  margin: 0 0 14px;
  height: 51px;
  width: 48.68%;
  display: flex;
  align-items: center;
  font-size: 12px;
  line-height: 15px;
  color: $color-dim-gray;
  border: 1px solid $color-very-light-gray;
  position: relative;
  background-color: transparent;

  @include vp-767 {
    flex-direction: column;
    width: 100%;
    height: 47px;
    justify-content: flex-start;
    margin: 0;
  }

  &:last-child {
    width: 100%;
    height: 57px;
    margin: 0;
  }

  &:first-child {
    height: 57px;
    transform: translateY(-6px);
  }

}

.contact__user input {
  height: 100%;
  width: 90%;
  border: none;
  font-size: 16px;
  line-height: 21px;
  align-self: flex-start;
  background-color: transparent;

  @include vp-767 {
    width: 85%;
  }

  &:focus {
    outline: none;
  }

  &::placeholder {
    color: $color-dim-gray;
  }
}

.contact__user button {
  background-color: transparent;
  border: none;
  width: 40px;
  height: 40px;
  cursor: pointer;

  @include vp-767 {
    position: absolute;
    top: 5px;
    right: 20px;
    width: 16px;
    height: 16px;
  }
}

.contact__delivery {
  padding-bottom: 40px;
}

.contact__delivery h3 {
  font-size: 28px;
  line-height: 30px;
  font-weight: 400;
  margin: 0 0 30px;

  @include vp-767 {
    font-size: 24px;
    line-height: 30px;
    margin: 0 0 24px;
  }
}

.contact__pay-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.contact__pay-item {
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 20px;
  padding-left: 32px;

  &:last-child {
    margin: 0;
  }
}

.contact__pay-item label {
  display: block;
  position: relative;
  cursor: pointer;
  user-select: none;
}

.contact__pay-input+label::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border: 4px solid $color-default-white;
  background-color: $color-default-white;
  top: 0;
  left: -31px;
  box-shadow: 0 0 0 1px $color-dim-gray;
  border-radius: 50%;
  transition: background-color 0.3s ease;
}

.contact__pay-input:checked+label::before {
  background-color: $color-default-black;
}

.contact__comment h3 {
  font-size: 28px;
  line-height: 30px;
  font-weight: 400;
  margin: 0 0 30px;

  @include vp-767 {
    font-size: 24px;
    line-height: 30px;
    margin: 0 0 24px;
  }

}

.contact__comment textarea {
  width: 100%;
  height: 141px;
  padding: 15px 20px;
  border: 1px solid $color-very-light-gray;

  &::placeholder {
    font-size: 16px;
    line-height: 21px;
    color: $color-dim-gray;
  }

  @include vp-767 {
    height: 120px;
  }
}

.contact__comment-length {
  font-size: 12px;
  line-height: 15px;
  color: $color-dim-gray;
  margin: 0;
  padding-left: 20px;
}
