.product {
  display: grid;
  grid-template-columns: 63.34% 33.33%;
  column-gap: 3.33%;
  padding-top: 45px;

  @include vp-1023 {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    column-gap: 0;
  }

  @include vp-767 {
    padding-top: 30px;
  }
}

.product__info-title {
  display: flex;
  align-items: flex-end;
  gap: 20px;
  margin-bottom: 57px;

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 27px;
  }
}

.product__info-title h2 {
  font-size: 40px;
  line-height: 42px;
  font-weight: 400;
  margin: 0;

  @include vp-767 {
    font-size: 28px;
    line-height: 30px;
  }
}

.card__title-breake {

  @include vp-767 {
    display: none;
  }
}

.product__info-links {
  display: flex;
  gap: 5px;
  font-size: 14px;
  line-height: 18px;
  padding-bottom: 5px;
}

.product__info-links a:first-child {
  color: $color-dim-gray;
}

.product__info-result {
  font-size: 28px;
  line-height: 30px;
  margin: 0 0 25px;

  @include vp-767 {
    font-size: 24px;
    line-height: 30px;
    width: 200px;
    margin: 0 0 20px;
  }
}

.product__list {
  border-top: 0.5px solid $color-very-light-gray;
}
