.container {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 200px;

  // @include vp-1439 {
  //   padding: 0 32px;
  // }

  @include vp-1599 {
    padding: 0 calc(200px - (1600px - 100vw) / (1600 - 1024) * (200 - 20));
  }

  @media (max-width: $vp-1024) {
    padding: 0 20px;
  }

  @media (max-width: $vp-768) {
    padding: 0 16px;
  }

  // @include vp-767 {
  //   padding: 0 16px;
  // }
}
