.card {
  display: grid;
  grid-template-columns: 47% 1fr 3%;
  padding-top: 25px;
  padding-bottom: 25px;

  @include vp-1439 {
    grid-template-columns: 1fr auto;
    grid-template-rows: auto auto;
    row-gap: 17px;
    padding-top: 17px;
    padding-bottom: 17px;
  }

  @include vp-767 {}
}

.card__info {
  display: grid;
  grid-template-columns: auto 1fr;
  column-gap: 20px;
  row-gap: 15px;

  @include vp-767 {
    column-gap: 12px;
  }

  @include vp-374 {
    display: flex;
    flex-wrap: wrap;
    gap: 0;
  }
}

.card__info img {
  grid-row-start: 1;
  grid-row-end: 3;
  order: 1;

  @include vp-767 {
    grid-row-start: 1;
    grid-row-end: 2;
    width: 90px;
  }
}

.card__title {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
  padding-top: 5px;
  // width: 220px;
  order: 2;

  @include vp-767 {
    font-size: 14px;
    line-height: 18px;
    width: 160px;
    padding-top: 0;
    padding-left: 12px;
  }
}

.card__param {
  grid-row-start: 2;
  grid-row-end: 3;
  order: 3;

  @include vp-767 {
    grid-column-start: 1;
    grid-column-end: 3;
    width: 200px;
    padding-top: 8px;
  }
}

.card__article,
.card__season {
  font-size: 12px;
  line-height: 15px;
  color: $color-dim-gray;
  margin: 0 0 10px;
}

.card__article {
  @include vp-374 {
    margin-bottom: 4px;
  }
}

.card__season {
  @include vp-374 {
    margin-bottom: 16px;
  }
}


.card__article span,
.card__season span {
  color: $color-default-black;
}

.card__size-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  list-style: none;
  margin: 0 0 8px;
  padding: 0;

  @include vp-767 {
    margin: 0 0 16px;
    gap: 9px;
  }
}

.card__size-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  background-color: $color-white-smoke;
  border-radius: 6px;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  cursor: pointer;
  user-select: none;
  transition: background-color 0.3s ease,
    color 0.3s ease-in;

  &--current {
    background-color: $color-default-black;
    color: $color-default-white;
  }

  &--disable {
    color: $color-very-light-gray;
  }
}

.card__color-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 5px;

  @include vp-767 {
    gap: 15px;
  }
}

.card__color-item {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid $color-default-white;
  cursor: pointer;
  transition: box-shadow 0.3s ease;

  &--disable {
    display: none;
  }

  &--current {
    box-shadow: 0 0 0 1px $color-default-black;
  }
}

[data-color="color-charcoal"] {
  background-color: $color-charcoal;
}

.card__color-item--current {}


[data-color="color-cerulean"] {
  background-color: $color-cerulean;
}

[data-color="color-golden-brown"] {
  background-color: $color-golden-brown;
}

.card__cost {
  display: flex;
  gap: 30px;
  justify-content: flex-end;
  padding-top: 5px;
  padding-right: 10px;

  @include vp-1439 {
    grid-row-start: 2;
    grid-row-end: 3;
    justify-content: space-between;
  }

  @include vp-767 {
    gap: 5px;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 0;
  }
}

.card__price-old {
  font-size: 12px;
  line-height: 15px;
  color: $color-dim-gray;
  text-decoration: line-through;

  &--disable {
    display: none;
  }
}

.card__price-single,
.card__price-total {
  height: 42px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card__price-new {
  font-size: 20px;
  line-height: 24px;
  // font-weight: 700;
  padding-top: 3px;

  @include vp-767 {
    font-size: 16px;
    line-height: 21px;
    padding: 0;
  }
}

.card__quantity {
  display: flex;
  height: 42px;
  width: 153px;
  background-color: $color-white-smoke;

  @include vp-374 {
    width: 147px;
  }
}

.card__button {
  flex-grow: 1;
  border: none;
  background-color: transparent;
  font-size: 24px;
  padding: 0;
  cursor: pointer;

  &--disable {
    color: $color-very-light-gray;
  }
}

.card__button-delete {
  align-self: flex-start;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;

  @include vp-767 {
    // padding-right: 10px;
  }
}

.card__count {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card__zero {
  grid-column-start: 1;
  grid-column-end: 4;
  justify-content: space-between;
  align-items: center;
  padding: 12px 25px;
  background-color: $color-white-smoke;
  display: none;

  @include vp-767 {
    padding: 12px 4px;
  }
}

.card__zero p {
  line-height: 21px;
  color: $color-dim-gray;
  margin: 0;
  padding-right: 25px;

  @include vp-767 {
    font-size: 14px;
    line-height: 18px;
    padding: 0 0 5px 0;
    width: 240px;
    // order: 1;
  }
}

.card__zero span {
  font-weight: 700;
}

.card__zero-link {
  flex-grow: 1;
  font-size: 14px;
  line-height: 18px;

  @include vp-374 {
    order: 2;
  }
}

.card__zero-button {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;

  @include vp-767 {
    align-self: flex-start;
    order: 1;
  }
}

.card.card--delete {
  row-gap: 0px;
}

.card--delete .card__zero {
  display: flex;
  flex-wrap: wrap;
}

.card--delete .card__info {
  display: none;
}

.card--delete .card__cost {
  display: none;
}

.card--delete .card__button-delete {
  display: none;
}
