.order {

  @include vp-1023 {
    grid-row-start: 4;
    grid-row-end: 5;
  }
}

.order__wrapper {
  border: 1px solid $color-dim-gray;
  padding: 25px 20px;
  position: relative;

  @include vp-767 {
    padding: 24px 16px;
  }
}

.order__list {
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 8px;
  padding-bottom: 15px;

  @include vp-767 {
    row-gap: 0;
    padding-bottom: 10px;
  }
}

.order__list dd {
  justify-self: end;
}

[data-info='products'],
[data-info='discount'],
[data-info='delivery'] {
  font-size: 20px;
  line-height: 24px;

  @include vp-767 {
    font: inherit;
  }
}

[data-info='total-price'],
[data-info='total-discount'],
[data-info='total-delivery'] {
  font-size: 20px;
  line-height: 24px;

  @include vp-767 {
    font: inherit;
  }
}

[data-info='products'],
[data-info='code'] {
  padding-bottom: 12px;

  @include vp-767 {
    padding-bottom: 8px;
  }
}

[data-info='promo'],
[data-info='code'] {
  line-height: 21px;

  @include vp-767 {
    font-size: 12px;
    line-height: 15px;
    padding-top: 6px;
    padding-bottom: 0;
  }
}

[data-info='total-discount'],
[data-info='total-promo'],
[data-info='total-code'] {
  color: $color-persian-red;
}

[data-info='total-promo'],
[data-info='total-code'] {

  @include vp-767 {
    font-size: 12px;
    line-height: 15px;
    align-self: flex-end;
  }
}

[data-info="delivery"] {

  @include vp-767 {
    padding-top: 10px;
  }
}

[data-info="total-delivery"] {
  align-self: flex-end;
}

.order__place {
  padding-top: 22px;
  padding-bottom: 18px;
  border-top: 1px solid $color-very-light-gray;
  border-bottom: 1px solid $color-very-light-gray;

  @include vp-767 {
    padding: 12px 0;
  }
}

.order__place label {
  font-size: 20px;
  line-height: 24px;
  margin: 0 0 7px;
  display: block;
  cursor: pointer;
  position: relative;
  user-select: none;

  @include vp-1279 {
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 10px;
  }
}

.order__place-input+label::before {
  content: "";
  position: absolute;
  width: 34px;
  height: 14px;
  border-radius: 7px;
  background-color: $color-very-light-gray;
  top: 6px;
  right: 0;
  transition: background-color 0.3s ease;
}

.order__place-input+label::after {
  content: "";
  position: absolute;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: $color-dim-gray;
  top: 3px;
  right: 18px;
  transition: right 0.3s ease;
}

.order__place-input:checked+label::after {
  right: 0;
}

.order__place-input:checked+label::before {
  background-color: $color-cerulean;
}

.order__place p {
  margin: 0;
  font-size: 12px;
  line-height: 15px;
  color: $color-dim-gray;
}

.order__total {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 20px 0;

  @include vp-767 {
    padding: 12px 0;
  }
}

.order__total h3,
.order__total p {
  font-size: 28px;
  line-height: 30px;
  font-weight: 700;
  margin: 0;

  @include vp-767 {
    font-size: 20px;
    line-height: 24px;
  }
}

.order__code {
  border: 1px solid $color-very-light-gray;
  background-color: transparent;
  width: 100%;
  height: 51px;
  padding: 15px 20px;
  margin-bottom: 20px;

  &:focus {
    outline: 1px solid $color-dim-gray;
  }

  @include vp-767 {
    height: 45px;
    margin-bottom: 10px;
  }
}

.order__button {
  font-size: 16px;
  line-height: 21px;
  padding: 15px 0;
  margin-bottom: 10px;
  width: 100%;
  color: $color-default-white;
  background-color: $color-default-black;
  border: none;
  cursor: pointer;
}

.order__prim {
  font-size: 14px;
  line-height: 18px;
  color: $color-dim-gray;
  margin: 0;
  text-align: center;
  width: 100%;
}

.order__prim a {
  color: $color-dim-gray;
  text-decoration: underline;
}
