.page-main {
  padding-top: 50px;

  @include vp-767 {
    padding-top: 22px;
  }
}

.page-main__broadcrumbs-list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
}

.page-main__broadcrumbs-item {
  padding-right: 24px;
  font-size: 12px;
  line-height: 15px;
  position: relative;

  &:nth-last-child(n+2)::after {
    content: "/";
    position: absolute;
    color: $color-dim-gray;
    top: 0;
    right: 12px;
  }
}

.page-main__broadcrumbs-item a {
  color: $color-dim-gray;
}
